  display: inline-block;
  width: 62px;
  height: 40px;
  border-radius: 12px;
  text-align: center;
  line-height: 40px;
  background-color: #7c3bed33;
  color: #7c3bed;
  font-size: 24px;
  margin-left: 20px;
}

.mePage {
  box-sizing: border-box;
  position: fixed;
  height: 100vh;
