  min-width: 90px;
  justify-content: center;
  margin-right: 40px;
}

.tabs .active{
  border-bottom: 1px solid #F5B22F;
  color: #F5B22F;
}
.shouyi{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 15px;
}

.shouyi .list{
  width: 50%;
  color: #fff;
  margin-bottom: 10px;
}

.dessc{
  color: #FFFFFF60;
}


.shouyi .list .val{
  font-size: 24px;
  font-weight: 600;
  color: #FFFFFF;
 
}

.shouyi .list .get{
    height: 30px;
    border-radius: 17px;
    width: 80px;
    background: #ffffff33;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
    color: #F5B22F;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .zhiboliu{
    width: 100%;
